import React from 'react';
import styles from './nav.module.css';
import { Link } from 'react-router-dom';

const Nav = () => {
    return (
        <div className={styles.container}>
            <Link to="/board/english"><p></p></Link>
            <Link to="/board/job"><p></p></Link>
            <Link to="/board/license"><p></p></Link>
        </div>
    );
};

export default Nav;