import React from 'react';
import Home from '../main/home/home';
import { Route,Routes } from 'react-router-dom'
import Layout from '../layout/layout';
import Login from './login/login';
import Member from './member/member';
import Board from './board/board';
import Write from './board/wrtie';


const Main = () => {
    return (
        <Routes>
            <Route element={<Layout/>}>
                <Route exact={true} path="/" element={<Home/>} />
                <Route path="/login" element={<Login/>}/>
                <Route path="/member" element={<Member/>}/>
                <Route path="/board/english" element={<Board kind="" ekind="ENGLISH" />}/>
                <Route path="/board/job" element={<Board kind="" ekind="JOB"/>}/>
                <Route path="/board/license" element={<Board kind="" ekind="LICENSE"/>}/>
                <Route path="/board/write/english" element={<Write kind="" ekind="ENGLISH"/>}/>
                <Route path="/board/write/job" element={<Write kind="" ekind="JOB"/>}/>
                <Route path="/board/write/license" element={<Write kind="" ekind="LICENSE"/>}/>
            </Route>
        </Routes>
    );
};

export default Main;